:root{--lightest: white;--light: rgb(228, 225, 254);--highlight: #f06;--light-grey: #c4c4c4;--middle-grey: #666;--dark-grey: #333;--darkest: black;--header-height: 12vh;--navbar-height: 8vh;--rest-of-height: 80vh}.button-submit-container input,.button-submit-container{font-weight:700;font-size:14px;text-transform:uppercase}.button-submit-container input{font-weight:bold;background-color:#f06;background-color:var(--highlight);color:white;color:var(--lightest);width:160px;height:60px;border-radius:60px;border:none;box-shadow:2px 2px 0 0px rgba(0,0,0,0.1)}.splash-image,.bg-image{position:fixed;width:100vw;height:auto;top:0;z-index:-1}body{background-color:black;background-color:var(--darkest);color:white;color:var(--lightest);font:14px "Roboto", "Lucida Grande", Helvetica, Arial, sans-serif}html,body{font-family:"roboto";margin:0;padding:0}a{color:white;color:var(--lightest);text-decoration:none}button:focus{outline:0}h1{font-style:normal;font-weight:500;font-size:36px;margin-left:25px}h2{font-style:normal;font-weight:500;font-size:24px}h3{font-style:normal;font-weight:normal;font-size:18px;line-height:24px}.header{width:100vw;height:12vh;height:var(--header-height);background-color:black;background-color:var(--darkest);position:fixed;top:0;border-bottom:1px solid #666;border-bottom:1px solid var(--middle-grey)}.scroll-container{position:fixed;top:12vh;top:var(--header-height);width:100vw;height:80vh;height:var(--rest-of-height);overflow-y:scroll;z-index:-1}nav.navbar{display:flex;justify-content:space-around;padding:0px 20px;background:black;background:var(--darkest);position:fixed;bottom:0;width:100vw;height:8vh;height:var(--navbar-height);border-top:1px solid #666;border-top:1px solid var(--middle-grey)}button{cursor:pointer;font-weight:bold}.close-x{position:fixed;top:15px;right:15px;cursor:pointer}.auth{width:90vw;height:100vh;margin:0 auto;display:flex;flex-wrap:wrap;justify-content:center;align-content:center}.splash-image{opacity:0;-webkit-animation-name:fadein;animation-name:fadein;-webkit-animation-duration:3s;animation-duration:3s;-webkit-animation-delay:1s;animation-delay:1s}@-webkit-keyframes fadein{0%{opacity:0}100%{opacity:1}}@keyframes fadein{0%{opacity:0}100%{opacity:1}}.logo{width:300px;height:auto;margin-bottom:40px;-webkit-animation-duration:0.5s;animation-duration:0.5s;-webkit-animation-timing-function:ease-in-out;animation-timing-function:ease-in-out;-webkit-animation-delay:3.5s;animation-delay:3.5s}@-webkit-keyframes blow{0%{opacity:1;width:300px}100%{opacity:0;width:800px}}@keyframes blow{0%{opacity:1;width:300px}100%{opacity:0;width:800px}}.inputAuthForms{width:85vw;display:block;font-size:18px;font-family:sans-serif;font-weight:300;color:#333;color:var(--dark-grey);box-sizing:border-box;border-radius:2em;background-color:white;background-color:var(--lightest);border:none;padding:21px;padding-left:32px;margin:20px}.inputAuthForms:focus{outline:0}.button-submit-container{width:85vw;display:flex;justify-content:flex-end;display:flex;align-items:center}.button-submit-container input{width:42vw;margin-left:10vw}.button-submit-container input:hover{outline:0}

.one-class {
  background-color: var(--dark-grey);
  margin-bottom: 1px;
  height: 90px;
  width: 100vw;
  display: flex;
  align-items: center;
}
.one-class div {
  margin-left: 16px;
}
.profile-pic img {
  width: 72px;
  height: auto;
  background-color: var(--lightest);
  -webkit-clip-path: circle(36px at center);
          clip-path: circle(36px at center);
}
.content {
  width: 100%;
}
.flechita {
  display: flex;
  justify-content: flex-end;
  margin-right: 32px;
}

/* --------FILTER TOOL-------------------------------------------------- */
.filter-tool {
  background-color: var(--lightest);
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  z-index: 2;
}

.button-filter {
  width: 100vw;
  position: fixed;
  bottom: 50px;
  display: flex;
  justify-content: center;
}

.button-filter button {
  background-color: var(--light-grey);
  padding: 20px 40px;
  border-radius: 40px;
  border: none;
  outline: none;
  height: 60px;
}

/* ----Filter Selector overlay------ */
.selector {
  width: 100vw;
  display: flex;
  justify-content: center;
  margin: 20px;
  height: 60px;
}
.selector button {
  padding: 20px;
  border-radius: 40px;
  border: none;
  outline: none;
  width: 80vw;
  height: 60px;
}

.filterItem {
  text-align: center;
  padding: 15px;
}


.button-pink-fixed {
    width: 100vw;
    position: fixed;
    bottom: 50px;
    display: flex;
    justify-content: center;
    
}

.button-pink-fixed button {
    background-color: var (--highlight);
    color: var (--lightest);
    padding: 20px 40px;
    border-radius: 40px;
    border: none;
    outline: none;
    height: 60px;
}
